@import '../../_shared';

.panel-background-primary {
  height: 100vh;
  width: 100%;
  position: fixed;
  overflow: hidden;

  .panel-background-secondary {

    .panel-background-secondary-top {
      // content: '';
      // position: absolute;
      // background-color: red;
      // height: 100%;
      // width: 100%;
      // z-index: 10;
      transform: translate(-100%, -100%);
      transition: transform 0.4s;
    }

    .panel-background-secondary-bottom {
      // content: '';
      // position: absolute;
      // background-color: pink;
      // height: 100%;
      // width: 100%;
      // z-index: 10;
      transform: translate(100%, 0);
      transition: transform 0.4s;
    }

    &.animating {
      .panel-background-secondary-top {
        transform: translate(0, -100%);
      }

      .panel-background-secondary-bottom {
        transform: translate(0, 0);
      }
    }

    > div {
      z-index: 1;
      position: absolute;
    }
  }

  div {
    width: 100%;
    height: 100%;
  }

  .panel-nav {
    left: 50%;
    position: absolute;
    transform: translateX(-50%);
    // https://github.com/Schepp/CSS-Filters-Polyfill
    filter: drop-shadow( 0px 2px 3px rgba(0, 0, 0, .7));
  
    > svg {
      position: relative;
      font-size: 40px;
      display: block;
      color: #fff
    }
  }

  .panel-nav-up {
    top: 0;
    margin-top: 10px;

    > svg:nth-child(2) {
      top: -20px;
    }
  }

  .panel-nav-down {
    bottom: 0;
    margin-bottom: 10px;

    > svg:nth-child(1) {
      top: 20px;
    }
  }
}