@import '../../_shared';

.container-options {
  text-align: center;

  h1 {
    display: inline-block;
  }

  &__scrollfade {
    position: relative;

    &::before {
      position: absolute;
      content: '';
      width: 100%;
      height: 0.75rem;
      background: linear-gradient(#ffffff, rgba(255, 255, 255, 0.001));
      top: 0;
      left: 0;
      z-index: 2;
    }

    &::after {
      position: absolute;
      content: '';
      width: 100%;
      height: 0.75rem;
      background: linear-gradient(rgba(255, 255, 255, 0.001), #ffffff);
      bottom: 0;
      left: 0;
      z-index: 2;
    }
  }

  &__child {
    max-height: 40vh;
    overflow-y: auto;
    position: relative;
    padding: 0.75rem 0 0.75rem 0;

    /* Hide scrollbar for IE and Edge */
    -ms-overflow-style: none;

    /* Hide scrollbar for Chrome, Safari and Opera */
    &::-webkit-scrollbar {
      display: none;
    }

    > div {
      position: relative;
    }
  }

  @media screen and (max-width: 350px) {
    .toggles--lg {
      svg {
        font-size: 2.5em !important;
      }

      .ribbon {
        font-size: 0.6rem !important;
      }
    }
  }
}