@import '../../_shared';

.container-portfolio {
  text-align: left;
}

.carousel {
  
  &__display {
    background-color: #262626;
    // height: 200px;
    white-space: nowrap;
  }

  &__img {
    width: auto;
    height: auto;
    display: inline-block;
    max-width: 200px;
    max-height: 200px;
    vertical-align: top;
    margin-left: 0.1em;
  }

  &__details {
    display: inline-block;
    position: relative;
    color: #ffffff;
    height: 100%;
    white-space: normal;

    &:after {
      position: absolute;
      content: '';
      width: 100%;
      height: 1rem;
      background: linear-gradient(rgba(255, 255, 255, 0.001), #262626);
      bottom: 0;
      left: 0;
      z-index: 2;
    }
  }

  &__title {
    display: inline-block;
    position: absolute;
    width: 100%;
    background-color: #262626;
    text-align: center;
    padding: 0;
    z-index: 3;

    &:after {
      position: absolute;
      content: '';
      width: 100%;
      height: 1rem;
      background: linear-gradient(#262626, rgba(255, 255, 255, 0.001));
      bottom: -1rem;
      left: 0;
      z-index: 2;
    }
  }

  &__links {
    content: '';
    position: absolute;
    height: 0px;
    z-index: 4;
    left: 0;
    top: 0;
    margin-top: 0.25rem;
  }

  &__link {
    position: relative;
    left: -50%;

    svg {
      width: 1.75rem !important;
      height: 1.75rem !important;
      padding: 0.25em;
      margin-top: 0.2rem;
      border-radius: 50%;
    }
  }

  &__description {
    position: relative;
    display: block;
    height: 200px;
    overflow-y: auto;
    font-size: 0.75em;
    z-index: 1;
    margin-left: 2rem;
    margin-right: 0.5rem;

    /* Hide scrollbar for IE and Edge */
    -ms-overflow-style: none;

    /* Hide scrollbar for Chrome, Safari and Opera */
    &::-webkit-scrollbar {
      display: none;
    }
  }

  &__text {
    margin-top: 2.25em;
  }

  &__tools {
    display: inline-block;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    vertical-align: bottom;
  }

  &__tool {
    color: #262626;
    font-weight: bold;
    padding: 0 0.4em 0 0.4em;
    margin: 0.25rem;
    display: inline-block;
    border-radius: 0.5rem;
  }

  &__controls {
    text-align: center;
    margin-top: 0.5rem;

    svg {
      display: inline-block;
      width: 100%;
      height: 100%;
      text-align: center;
      vertical-align: top;
      font-size: 1rem;
    }
  }

  &__item {
    vertical-align: middle;
    width: 0.85rem;
    height: 0.85rem;
    margin: 0 0.15rem 0 0.15rem;
    border-radius: 50%;
    display: inline-block;
    background-color: #262626;
  }

  &__back {
    vertical-align: middle;
    background-color: #262626;
    display: inline-block;
    margin: 0 0 0 0.25rem;
    border-radius: 50%;
    width: 2rem !important;
    height: 2rem !important;
    color: #ffffff;
  }

  &__next {
    vertical-align: middle;
    background-color: #262626;
    display: inline-block;
    margin: 0 0.25rem 0 0;
    border-radius: 50%;
    width: 2rem !important;
    height: 2rem !important;
    color: #ffffff;
  }

  @media screen and (min-width: 601px) {

    &__links {
      width: calc(1.75rem + 0.25em * 2 + 0.15rem * 2 + 8px);
    }

    &__link {
      svg {
        color: #262626;
        background-color: #ffffff;
        border: 0.15rem solid #262626;
      }
    }
  }

  @media screen and (max-width: 600px) {
    tr, td {
      display: block;
    }

    img {
      display: none;
    }

    &__description {
      height: 20vh;
      margin-left: 0.5rem;
    }

    &__links {
      position: static;
      left: 0;
      margin: 0;
      float: right;
    }

    &__link {
      position: static;

      svg {
        width: 1em !important;
        height: 1em !important;
        // margin: 0;
        padding: 0;
        margin-right: 0.25rem;
      }
    }
  }
}