@import '../../_shared';

.toggles {
  $self:&;
  text-align: center;

  &__toggle {
    display: inline-block;

    &:hover {
      cursor: pointer;
    }
  }

  &--sm {
    position: absolute;
    right: 1rem;
    top: 1rem;

    &#{ $self }--mobile {
      position: static;
      display: inline-block;
      margin-top: 1rem;
    }

    &#{ $self }--hidden {
      display: none;
    }

    #{ $self }__toggle {
      transition: background-color 0.15s;
      background-color: #262626;
      color: #fff;
      padding: 0.25em;
      width: 1.5em !important;
      height: 1.5em !important;
      margin-left: 0.15em;
      margin-right: 0.15em;
    }
  }

  &--lg {

    &#{ $self }--hidden {
      display: none;
    }
    
    #{ $self }__toggle {
      margin: 1em 1em 0 1em;

      &--animating {
        div {
          color: #ffffff;
          background-color: #262626;
        }
        
        .ribbon {
          opacity: 0;
        }
      }

      div {
        transition: transform 0.5s;
      }
      
      svg {
        font-size: 3.5em;
        padding: 0.25em;
        width: 1.5em !important;
        height: 1.5em !important;
      }
    }
  }
}